@import 'base';
@import '../../vendors/bootstrap/dropdown';
@import '../../vendors/bootstrap/buttons';
@import '../../vendors/bootstrap/button-group';

.show > .dropdown-menu {
    display: flex;
    flex-wrap: wrap;
}
/* ========================================================================== */
/* ========================================================================== */

html, body {
    padding:0; margin:0;
    font-family: 'Trebuchet MS', Helvetica, 'DejaVu Sans', 'Lucida Grande', 'Lucida Sans Unicode', 'Lucida Sans', sans-serif;
    height: 100%;
    font-size:16px;
}

h1, h2, h3 { margin-top:0; font-weight: normal; color:#34495e; }

h1 {
    color:#34495e;
    background: #F9F9F9;
    border-bottom: solid 1px #EDEDED;
    margin:-20px;
    margin-bottom: 20px;
    padding:0 20px;
    font-size:22px;
    height:50px;
    line-height:50px;

}
h1+h2 {
    margin-top: -5px;
}
h1 span, h2 span { color:#68809B; }
h1 span a { color: inherit;}

a, a.ajaxlink { color: #34495e; transition: color ease-in-out .15s; }
a:hover, a.ajaxlink:hover { color: #f90; }

a.ajaxlink { text-decoration:none; border-bottom: dashed 1px #34495e; }
a.ajaxlink:hover { text-decoration:none; border-bottom: dashed 1px #f90; }

.positive { color:green; }
.negative { color:red; }

.modal_padding { padding:10px; }
.modal_treeview {
    padding:10px;
    width:300px;
    height:300px;
    overflow-y: auto;
    position: relative;
    margin-bottom: 10px;
}

.modal_treeview .hint{
    font-size:small;
    color:#999;
    display: inline-block;
    margin-bottom: 10px;
}

.loading-overlay{
    position: fixed;
    width:100%;
    height:100%;
    left:0;
    top:0;
    background-image: url("../../images/admin/ajax-loader.gif");
    background-repeat: no-repeat;
    background-position: center center;
    z-index: 100;
}
.loading-overlay ~ #wrapper, .loading-overlay ~ #wrapper ~ #cp_footer  {
    -webkit-filter:blur(1px);
    -moz-filter:blur(1px);
    -ms-filter:blur(1px);
    -o-filter:blur(1px);
    filter:blur(1px);
}
/* ========================================================================== */
/* ========================================================================== */

#cp_body{
    padding:20px;
    height:100%;
    position: relative;
}

#wrapper{
    position:relative;
    height:auto !important;
    min-height: 100%;
    display:block;
}
#popup-manager.popup-show ~ #wrapper, #popup-manager.popup-show ~ #cp_footer {
    -webkit-filter:blur(2px);
    -moz-filter:blur(2px);
    -ms-filter:blur(2px);
    -o-filter:blur(2px);
    filter:blur(2px);
}
#cp_body .pad { height:30px; clear:both; }

#cp_footer{
    height:30px;
    font-size:12px;
    line-height:30px;
    background: #333;
    color:#FFF;
    margin-top:-30px;
    padding:0 20px;
    text-align: center;
    clear:both;
    position: relative;
    z-index:9;
    color:#999;
}

#cp_footer a { color:#999; }
#cp_footer a:hover { text-decoration: none; }

/* ========================================================================== */
/* ========================================================================== */

#cp_top_line{
    background: #333;
    height:35px;
    line-height:35px;
    padding:0 10px;
    color:#FFF;
    font-size:12px;
}

#cp_top_line #site_off_notice{
    float:left;
    color: #F90;
    padding-left:20px;
    background:url("../../images/icons/error.png") no-repeat left center;
}

#cp_top_line ul, #cp_top_line li { margin:0; padding:0; list-style:none; }
#cp_top_line ul { overflow: hidden; }
#cp_top_line ul li { float: left; padding:0 10px; border-right:solid 1px #444; }
#cp_top_line ul li:hover { background-color: #444; }
#cp_top_line ul li:last-child { border:none; }

#cp_top_line ul li a{ color:#999; }
#cp_top_line ul li a:hover { color:#F90; }

#cp_top_line #left_links{ float:left; }
#cp_top_line #right_links{ float:right; }

#cp_top_line a.logout {
    color:#6FB8F1;
}

#cp_top_line a.user{
    display: block;
    height: 35px;
}
#cp_top_line a.user > img {
    float: left;
    padding: 4px 0;
    height: 27px;
    width: 27px;
    border-radius: 50%;
}
#cp_top_line a.user > span {
    float: left;
    margin: 0 0 0 10px;
}

#cp_top_line #is_update a {
    padding-left:18px;
    background: url("../../images/icons/info.png") no-repeat left 1px;
    text-decoration: none;
    color:#DDD;
}

#cp_top_line #is_update a:hover {
    color:#F90;
}

