.cp_toolbar {
    padding:5px 10px;
    background:#F9F9F9;
    border: solid 1px #EDEDED;
    border-radius: 4px;
    margin-bottom:10px;
    transition: all ease-in-out .2s;
}
.fixed_toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: 0;
    z-index: 101;
    box-shadow: 0px 2px 10px #B3C2C9;
    padding: 10px 21px;
    box-sizing: border-box;
    border-top: none;
    border-right: none;
    border-left: none;
    border-color: #B3C2C9;
}
.cp_toolbar ul,
.cp_toolbar li {
    overflow:hidden;
    padding:0;
    margin:0;
}

.cp_toolbar ul li { float:left; }

.cp_toolbar a {
    color:#666; text-decoration: none;
    padding:4px 0px;
    margin-right: 10px;
}

.cp_toolbar a:hover{
    text-decoration: underline;
    color:#000;
}

.cp_toolbar a{
    display: inline-block;
    padding:1px;
    padding-left: 20px;
}

.cp_toolbar .important a{ color: #bf0000; }

.cp_toolbar .add .nav-link { background: url("../../images/icons/add.png") no-repeat left center; }
.cp_toolbar .add_folder .nav-link { background: url("../../images/icons/folder_add.png") no-repeat left center; }
.cp_toolbar .edit_folder .nav-link { background: url("../../images/icons/folder_edit.png") no-repeat left center; }
.cp_toolbar .delete_folder .nav-link { background: url("../../images/icons/folder_delete.png") no-repeat left center; }
.cp_toolbar .tree_folder .nav-link { background: url("../../images/icons/folders_explorer.png") no-repeat left center; }
.cp_toolbar .edit .nav-link { background: url("../../images/icons/edit.png") no-repeat left center; }
.cp_toolbar .delete .nav-link { background: url("../../images/icons/delete.png") no-repeat left center; }
.cp_toolbar .move .nav-link { background: url("../../images/icons/move_to_folder.png") no-repeat left center; }
.cp_toolbar .save .nav-link { background: url("../../images/icons/save.png") no-repeat left center; }
.cp_toolbar .cancel .nav-link { background: url("../../images/icons/cancel.png") no-repeat left center; }
.cp_toolbar .permissions .nav-link { background: url("../../images/icons/key.png") no-repeat left center; }
.cp_toolbar .filter .nav-link { background: url("../../images/icons/filter.png") no-repeat left center; }
.cp_toolbar .delete_filter .nav-link { background: url("../../images/icons/filter_delete.png") no-repeat left center; }
.cp_toolbar .settings .nav-link { background: url("../../images/icons/settings.png") no-repeat left center; }
.cp_toolbar .refresh .nav-link { background: url("../../images/icons/refresh.png") no-repeat left center; }
.cp_toolbar .help .nav-link { background: url("../../images/icons/help.png") no-repeat left center; }
.cp_toolbar .install .nav-link { background: url("../../images/icons/install.png") no-repeat left center; }
.cp_toolbar .logs .nav-link { background: url("../../images/icons/logs.png") no-repeat left center; }
